.mainField {
    width: 50%;
    float: left;
}
.field {
    background: white;
    border-radius: 10px;
    border: 5px solid gray;
    width: 70%;
    margin: 0 auto;
    padding: 1vw 0;
}
.mainField h2 {
    color: #ec1414;
    font-size: 2vw;
    margin-bottom: 0.5vw;
}
.totalCases {
    color: #0a0fff;
    font-size: 1.5vw;
    margin-bottom: 0.5vw;
}
.recovered {
    color: #ff762c;
    font-size: 1.5vw;
    margin-bottom: 0.5vw;
}
.deaths {
    font-size: 1.5vw;
}