.searchField {
    margin-bottom: 3vw;
}
.dropList {
    text-align: left;
    width: 300px;
    margin: 0 auto;
    max-height: 282px;
    overflow: hidden;
    position: absolute;
    left: -60px;
    right: 0;
    z-index: 100;
}
.dropList ul li {
    list-style: none;
    background: #f6f6f6;
    border: 1px solid #ddd;
    margin-top: -1px;
    padding: 8px;
    font-size: 26px;
    cursor: pointer;
}
.dropList ul li:hover{
    background: #eee;
}
.input {
    font-size: 26px;
}
.searchBtn {
    padding: 0 5px 3px;
    outline: none;
}